  let response
  try {
    if (url !== 'search') {
      response = await axios.get(`${appUrl}/index/${url}/${page}`, {
        headers: {'Authorization': `Bearer ${localStorage.getItem('jwt_token')}`}
      })
    } else {
      response = await axios.post(`${appUrl}/index/${url}/${page}`, 
